/* Base / Reset */
* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: system-ui,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Inter,"Helvetica Neue",Arial,sans-serif;
  background:#000;
  color:#eee;
  -webkit-font-smoothing: antialiased;
}

/* Generic element defaults */
h1,h2,h3,h4 { font-weight:600; line-height:1.15; margin:0 0 .75rem; }
p { margin:.4rem 0; }
ul { margin:.4rem 0 0; padding-left:1.1rem; }
li { margin:.25rem 0; }

/* Container (used in prototype & full version) */
.p-4 { padding:1rem; }
.max-w-xl { max-width:42rem; }
.mx-auto { margin-left:auto; margin-right:auto; }
.text-center { text-align:center; }
.text-left { text-align:left; }

/* Simple utility (prototype) */
.mb-2 { margin-bottom:.5rem; }
.mb-4 { margin-bottom:1rem; }
.mt-4 { margin-top:1rem; }
.space-y-2 > * + * { margin-top:.5rem; }
.space-y-4 > * + * { margin-top:1rem; }
.font-bold { font-weight:700; }
.font-semibold { font-weight:600; }
.text-xs { font-size:.75rem; }
.text-sm { font-size:.875rem; }
.text-lg { font-size:1.125rem; }
.text-2xl { font-size:1.5rem; }
.opacity-70 { opacity:.7; }

.border { border:1px solid #2a2a2a; }
.rounded { border-radius:.5rem; }
.shadow { box-shadow:0 4px 16px -4px rgba(0,0,0,.55); }

.bg-blue-600 { background:#1d4ed8; }
.bg-blue-600:hover { background:#285ce7; }
.bg-gray-700 { background:#374151; }
.bg-gray-700:hover { background:#445166; }
.bg-green-600 { background:#15803d; }
.bg-green-600:hover { background:#1d974a; }
.bg-red-600 { background:#dc2626; }
.bg-red-600:hover { background:#ef3434; }
.text-white { color:#fff; }
.text-neutral-200 { color:#e5e5e5; }
.text-neutral-300 { color:#d4d4d4; }

.p-2 { padding:.5rem; }
.p-3 { padding:.75rem; }
.p-6 { padding:1.5rem; }
.px-2 { padding-left:.5rem; padding-right:.5rem; }
.px-4 { padding-left:1rem; padding-right:1rem; }
.py-0\.5 { padding-top:.125rem; padding-bottom:.125rem; }
.py-1 { padding-top:.25rem; padding-bottom:.25rem; }
.py-2 { padding-top:.5rem; padding-bottom:.5rem; }
.py-4 { padding-top:1rem; padding-bottom:1rem; }
.w-full { width:100%; }
.w-40 { width:10rem; }
.rounded-3xl { border-radius:1.75rem; }

/* Buttons (general) */
button {
  font-family:inherit;
  cursor:pointer;
  font-weight:600;
  letter-spacing:.3px;
  border:none;
  transition:background .2s, transform .15s;
}
button:disabled {
  opacity:.55;
  cursor:not-allowed;
}
button:active { transform:translateY(1px); }

/* Inputs */
input {
  background:#101010;
  border:1px solid #333;
  color:#eee;
  border-radius:8px;
  font-size:.95rem;
  transition:border-color .18s, background .18s;
}
input:focus {
  outline:none;
  border-color:#ff3737;
  background:#151515;
}

/* ===== Scanner Modal (for full QR version) ===== */

.fixed { position:fixed; }
.inset-0 { inset:0; }
.flex { display:flex; }
.items-center { align-items:center; }
.justify-center { justify-content:center; }
.bg-black\/70 { background:rgba(0,0,0,.7); }
.z-50 { z-index:50; }
.relative { position:relative; }
.mx-auto { margin-left:auto; margin-right:auto; }
.mb-6 { margin-bottom:1.5rem; }
.mb-8 { margin-bottom:2rem; }
.mt-2 { margin-top:.5rem; }
.mt-3 { margin-top:.75rem; }
.gap-3 { gap:.75rem; }
.gap-2 { gap:.5rem; }
.gap-4 { gap:1rem; }

.bg-neutral-900 { background:#111; }
.bg-neutral-700 { background:#2d2d2d; }
.bg-neutral-700:hover { background:#3a3a3a; }
.hover\:bg-neutral-600:hover { background:#4a4a4a; }

.text-green-400 { color:#4ade80; }
.text-red-400 { color:#f87171; }

.object-cover { object-fit:cover; }

/* Scanner frame (from full version) */
.relative.rounded-2xl {
  border-radius:1rem;
}

/* Overlays */
.bg-black\/40 { background:rgba(0,0,0,.4); }

/* Additional styling for polished QR modal (optional minimal) */
.scan-video {
  background:#000;
  width:100%;
  height:100%;
  display:block;
  transform:translateZ(0);
}

/* Provide a subtle red border pulse optional (comment out if not wanted) */
@keyframes scanPulse {
  0%,100% { box-shadow:0 0 0 0 rgba(220,38,38,0.7); }
  50% { box-shadow:0 0 12px 4px rgba(220,38,38,0.3); }
}
.border-red-600 {
  border-color:#dc2626;
  animation:scanPulse 3s infinite;
}

/* Mobile tweaks */
@media (max-width:640px) {
  h1 { font-size:1.85rem; }
  .text-2xl { font-size:1.35rem; }
  .rounded-3xl { border-radius:1.4rem; }
}

/* Dark scrollbar (desktop) */
body::-webkit-scrollbar { width:10px; }
body::-webkit-scrollbar-track { background:#050505; }
body::-webkit-scrollbar-thumb { background:#262626; border-radius:6px; }
body::-webkit-scrollbar-thumb:hover { background:#343434; }
